@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
  font-family: 'Montserrat', sans-serif;
}

h1 {
  font-size: 3rem;
}

canvas {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -999;
}
.title{text-align:center;margin-top:1rem;margin-bottom:1rem;position:absolute;top:1rem;width:100%}@media (min-width: 961px){.title{-webkit-transform:rotateZ(-90deg) translate(0, -50%);-moz-transform:rotateZ(-90deg) translate(0, -50%);-o-transform:rotateZ(-90deg) translate(0, -50%);transform:rotateZ(-90deg) translate(0, -50%);left:-100px;top:45%;width:auto}}

.image-flipper{display:-webkit-flex;display:-moz-box;display:flex;-webkit-flex-direction:column;-moz-box-orient:vertical;-moz-box-direction:normal;flex-direction:column;-webkit-justify-content:center;-moz-box-pack:center;justify-content:center;-webkit-align-items:center;-moz-box-align:center;align-items:center;height:100vh}.image-flipper__description{text-align:center;margin-top:1rem;margin-bottom:1rem}.image-flipper__uploader{display:block;margin:0 auto;margin-bottom:1rem}

.image-viewer{max-width:100%;position:relative;margin:0 auto;overflow:hidden}.image-viewer__image{max-width:80vw;max-height:80vh;-webkit-transition:-webkit-transform 0.5s ease-in-out;transition:-webkit-transform 0.5s ease-in-out;-o-transition:-o-transform 0.5s ease-in-out;-moz-transition:transform 0.5s ease-in-out, -moz-transform 0.5s ease-in-out;transition:transform 0.5s ease-in-out;transition:transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out, -moz-transform 0.5s ease-in-out, -o-transform 0.5s ease-in-out}.image-viewer__buttons{position:relative}.image-viewer__button{position:absolute}.image-viewer__button--top-left{top:0;left:0;-webkit-transform:rotateZ(-45deg);-moz-transform:rotateZ(-45deg);-o-transform:rotateZ(-45deg);transform:rotateZ(-45deg)}.image-viewer__button--top{top:0;left:50%;-webkit-transform:translate(-50%, 0%);-moz-transform:translate(-50%, 0%);-o-transform:translate(-50%, 0%);transform:translate(-50%, 0%)}.image-viewer__button--top-right{top:0;right:0;-webkit-transform:rotateZ(45deg);-moz-transform:rotateZ(45deg);-o-transform:rotateZ(45deg);transform:rotateZ(45deg)}.image-viewer__button--left{top:50%;left:0;-webkit-transform:translate(0%, -50%);-moz-transform:translate(0%, -50%);-o-transform:translate(0%, -50%);transform:translate(0%, -50%)}.image-viewer__button--right{top:50%;right:0;-webkit-transform:translate(0%, -50%);-moz-transform:translate(0%, -50%);-o-transform:translate(0%, -50%);transform:translate(0%, -50%)}.image-viewer__button--bottom-left{bottom:0;left:0;-webkit-transform:rotateZ(45deg);-moz-transform:rotateZ(45deg);-o-transform:rotateZ(45deg);transform:rotateZ(45deg)}.image-viewer__button--bottom{bottom:0;left:50%;-webkit-transform:translate(-50%, 0%);-moz-transform:translate(-50%, 0%);-o-transform:translate(-50%, 0%);transform:translate(-50%, 0%)}.image-viewer__button--bottom-right{bottom:0;right:0;-webkit-transform:rotateZ(-45deg);-moz-transform:rotateZ(-45deg);-o-transform:rotateZ(-45deg);transform:rotateZ(-45deg)}

.button{border:none;background:transparent;outline:none;font-size:1.5rem;color:white;padding:1.5rem}

